:root{
  --maxWidth: 1280px;
  --white: #fff;
  --lightGrey: #eee;
  --darkGrey:#1c1c1c;
  --medGrey:#353535;
  --fontBig: 2.5rem;
  --fontMed: 1.2rem;
  --fontSmall: 1rem
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif;
  /* background-color: #3f3f3f; */
}
 
h1{
  font-weight: 600;
  font-size: 2rem;
  color: #fff;
  color: var(--white);
}

h3 {

  font-weight: 600;
  font-size: 1.1rem;
}

p {
  font-size: 1rem;
  font-size: var(--fontSmall);
  color: #fff;
  color: var(--white);
}

input:focus {
  outline: none;
}
