.movieBar {
  background-color: var(--darkGrey);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 20px 25px;

}

.movieBar section {
  background-color: var(--medGrey);
  border-radius: 16px;
  padding: 2px 20px;
}

@media screen and (max-width: 550px) {

  .movieBar {
   flex-direction: column;

  }
}